.header {
  margin-top: 60px;

}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Lora', serif;
  color: #444;
}

.headerTitleSmall {
  position: absolute;
  top: 8%;
  font-size: 20px;
}

.headerTitleLarge {
  position: absolute;
  top: 9%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
  object-fit: cover;
}